// Default root setting
:root {
  // Root measurement adjustment
  font-size: 16px;

  // Dimension adjustment
  --sidebar-width: 50px;
  --chatbox-width: 60; // Don't put unit, value in percentage
  --leftbox-width: 40; // Don't put unit, value in percentage
  --button-area-height: 65px;
  --icon-button-dimension: 40px;
  --dialog-input-height-min: 65px;
  --dialog-input-height-max: 155px;
  --avatar-dimension: 90px;
  --layout-sidebar-adjustment: 0px;
  --mobile-menu-button-translation: 20px;
  --mobile-chatbox-button: 50px;
  --mobile-chatbox-button-translation: 20px;
  --checkbox-indicator-dimension: 30px;

  // Theme color adjustment
  --brand-fill: #D53A4C;
  --brand-fill-test: #D53A4C66;
  --brand-gradient: linear-gradient(90.54deg, #F25767 0%, #D83445 100%);
  --light-background: #FFFFFF;
  --base-background: #E5E5E5;
  --text-dark: #707C97;
  --text-light: rgba(112, 124, 151, 0.7);
  --shadow: rgba(32, 32, 32, 0.08);
  --breakline: rgba(112, 124, 151, 0.15);
  --icon-light: white;
  --icon-dark: rgba(112, 124, 151, 0.5);
  --input-label: rgba(17, 17, 17, 0.48);
  --chat-bubble-bot: var(--brand-gradient);
  --chat-bubble-bot-tail: #F15666;
  --chat-bubble-user: white;
  --chat-bubble-user-border: rgba(112, 124, 151, 0.25);
  --chat-bubble-user-tail: var(--chat-bubble-user-border);
  --dropdown-hover: #F1F1F1;
  --dropdown-shadow: 0px 4px 4px rgba(51, 51, 51, 0.15);
  --inactive: var(--input-label);

  // Theme standardisation
  --box-shadow: 0 4px 10px var(--shadow);
  --selected-box-shadow: 0 0 10px var(--brand-fill-test);
}

* {
  box-sizing: border-box;
  font-size: 16px;
  margin: 0;
  padding: 0;
  font-family: Proxima Nova, Helvetica, sans-serif;
  scrollbar-color: transparent transparent;
  transition: all 0.1s ease-in-out;
  &::-webkit-scrollbar {
    width: 0px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }
  &::-webkit-scrollbar-thumb {
    background-color: var(--brand-fill);
    box-shadow: inset 0 0 10px var(--brand-fill);
    border-radius: 200px;
  }
  &:hover {
    scrollbar-color: var(--brand-fill) transparent;
    transition: all 0.1s ease-in-out;
    &::-webkit-scrollbar {
      width: 6px;
    }
  }
}

input {
  background: none;
  outline: none;
  border: none;
}

button:focus {
  outline: none;
  border: none;
}

// Adjustment for button styling (global)
.Button {
  cursor: pointer;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  &:hover {
    .Icon-Rotate {
      transform: rotate(90deg);
      transition: transform 0.2s ease-in-out;
    }
  }
  &:disabled {
    background: var(--base-background);
    box-shadow: none;
    cursor: default;
    transition: all 0.2s ease-in-out;
    // transition-property: background-color, box-shadow, background;
  }
  &-FitContent {
    width: fit-content;
    height: fit-content;
    padding: 15px 25px;
    background-color: var(--brand-fill);
    color: var(--light-background);
    box-shadow: 0 4px 10px -3px var(--brand-fill);
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
    transition-property: background-color, box-shadow, opacity;
    &:hover {
      opacity: 0.8;
      transition: opacity 0.2s ease-in-out;
    }
  }
  &-Full {
    width: 100%;
    height: 100%;
    background-color: var(--brand-fill);
    color: var(--light-background);
    border-radius: 4px;
    box-shadow: 0 4px 10px -3px var(--brand-fill);
    transition: all 0.2s ease-in-out;
    transition-property: background-color, box-shadow, opacity;
    &:hover {
      opacity: 0.8;
      transition: opacity 0.2s ease-in-out;
    }
  }
  &-Hamburger {
    background-color: inherit;
    width: var(--sidebar-width);
    height: var(--sidebar-width);
  }
  // &-Send {
  //   &:hover {
  //     .Icon-Send {

  //     }
  //   }
  // }
}

.Icon {
  &-Rotate {
    transition: transform 0.2s ease-in-out;
  }
  &-Light {
    color: var(--icon-light);
  }
  &-Dark {
    color: var(--icon-dark);
  }
  &-Hamburger {
    width: 45%;
    height: 45%;
  }
  &-Add, &-Audible, &-Smiley {
    width: 60%;
    height: 60%;
  }
  &-Down {
    width: 80%;
    height: 80%;
  }
  &-Close {
    width: 95%;
    height: 95%;
  }
  &-Check {
    width: 100%;
    height: 100%;
  }
}

.Input {
  &Box {
    &-Container {
      background-color: var(--light-background);
      box-shadow: var(--box-shadow);
      margin-bottom: 20px;
      padding: 10px 15px;
      display: flex;
      flex-direction: column;
      transition: box-shadow 0.3s ease-in-out;
      border-radius: 4px;
      &:last-child {
        margin-bottom: 0;
      }
      input {
        font-size: 1rem;
        width: 100%;
      }
    }
    &-Label {
      font-size: 0.8rem;
      margin-bottom: 5px;
      color: var(--input-label);
    }
    &-InputArea {
      display: flex;
      flex-direction: row;
    }
    &-Currency {
      margin-right: 0.6rem;
      font-size: 1rem;
    }
  }
  &Dropdown {
    &-Wrapper {
      width: 100%;
      position: relative;
    }
    &-Container {
      position: relative;
      cursor: pointer;
      flex-direction: row;
      align-items: stretch;
      justify-content: center;
      z-index: 20;
    }
    &-Thumb {
      width: 100%;
    }
    &-Action {
      width: fit-content;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }
    &-Button {
      background: transparent;
      width: 35px;
      height: 35px;
    }
    &-List {
      position: absolute;
      display: flex;
      width: inherit;
      flex-flow: column wrap;
      height: fit-content;
      background: white;
      top: calc(100% + 10px);
      border-radius: 4px;
      box-shadow: var(--dropdown-shadow);
      overflow: hidden;
      padding: 5px 0;
      z-index: 19;
      max-height: 50vh;
      overflow: auto;
      ul {
        list-style: none;
      }
      li {
        padding: 15px;
        font-size: 0.9rem;
        cursor: pointer;
        &:hover {
          background-color: var(--dropdown-hover);
        }
      }
      &[data-show='true'] {
        opacity: 1;
        transition: all 0.1s ease-in-out;
      }
      &[data-show='false'] {
        pointer-events: none;
        opacity: 0;
        top: 0;
        transition: all 0.1s ease-in-out;
      }
    }
  }
}

// Remove arrows for input number
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

