// Adjustment for main layout
.AIVI {
  &-Page {
    width: 100vw;
    height: 100vh;
    background-color: var(--base-background);
    display: flex;
    flex-flow: row wrap;
  }
  
  &-Sidebar {
    height: 100%;
    width: var(--sidebar-width);
    background-color: var(--brand-fill);
  }

  &-Chatbox {
    height: 100%;
    width: calc(var(--chatbox-width) / 100 * (100% - var(--sidebar-width) + var(--layout-sidebar-adjustment)));
    padding: 35px;
    &-Mobile {
      &-Model {
        display: none;
      }
      &-Button {
        display: none;
      }
    }
  }

  &-Leftbox {
    height: 100%;
    width: calc(var(--leftbox-width) / 100 * (100% - var(--sidebar-width) + var(--layout-sidebar-adjustment)));
    background-color: var(--light-background);
    padding: 50px 35px;
    display: flex;
    flex-flow: column wrap;
    align-items: stretch;
    overflow: hidden;
  }
}

// Adjustment for chatbox
.Chatbox {
  &-Container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    position: relative;
  }
  &-Avatar {
    width: var(--avatar-dimension);
    height: var(--avatar-dimension);
    border-radius: 50%;
    position: relative;
    z-index: 3;
    display: flex;
    align-items: center;
    justify-items: center;
    background-color: var(--light-background);
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
  &-Box {
    width: 100%;
    height: calc(100% - var(--avatar-dimension));
    background-color: var(--light-background);
    box-shadow: var(--box-shadow);
    border-radius: 2px;
    position: relative;
    top: calc((var(--avatar-dimension) / 2) * -1);
    padding: var(--avatar-dimension) 20px 0;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;
  }
  &-Dialog {
    height: calc(100% - var(--dialog-input-height) - 15px);
    margin-bottom: 10px;
    overflow: auto;
    padding: 0 20px;
  }
  &-Input {
    height: fit-content;
    min-height: var(--dialog-input-height-min);
    max-height: var(--dialog-input-height-max);
    border-top: 1px solid var(--breakline);
    display: flex;
    align-items: center;
    padding: 10px 0;

    &-UploadOptions, &-Send, &-Audible, &-Smiley {
      width: var(--icon-button-dimension);
      height: var(--icon-button-dimension);
      background: var(--brand-gradient);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 10px;
    }
    &-Audible, &-Smiley {
      background: none;
    }
    &-Send, &-Audible {
      margin-right: 0;
    }
    &-InputBox {
      font-size: 1rem;
      width: calc(100% - (3 * var(--icon-button-dimension)) - 20px);
      border: none;
      padding: 10px 15px;
      border-radius: 2rem;
      margin-right: 10px;
      resize: none;
      display: flex;
      align-items: center;
      justify-content: center;
      white-space: normal;
      &:focus {
        box-shadow: 0 0 4px var(--brand-fill-test);
      }
    }
  }
}

// Adjustment for leftbox
.Leftbox {
  &-Submitted {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column wrap;
    p {
      font-size: 1.1rem;
      font-weight: 700;
      color: var(--text-dark);
      text-align: center;
    }
    img {
      margin: 30px 0;
    }
  }
}

.CashFromCard {
  &-Content {
    height: 20px;
    overflow: auto;
    height: calc(100% - var(--button-area-height));
    width: 100%;
  }
  &-Button {
    height: var(--button-area-height);
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &-Title {
    font-size: 1rem;
    color: var(--text-dark);
    margin-bottom: 20px;
    font-weight: 700;
  }
  &-Description {
    font-size: 0.85rem;
    line-height: 1.3rem;
    color: var(--text-dark);
  }
  &-Details {
    font-size: 0.85rem;
    padding: 20px 10px;
    max-height: calc(100% - 120px);
  }
}

.CreditCard {
  &-Content {
    height: 20px;
    overflow: auto;
    height: calc(100% - var(--button-area-height));
    width: 100%;
  }
  &-Button {
    height: var(--button-area-height);
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.TabBar {
  &-Header {
    display: flex;
    flex-flow: row nowrap;
    height: fit-content;
    list-style: none;
    align-items: center;
    margin-bottom: 20px;
    width: 100%;
    overflow: auto;
    scrollbar-color: transparent transparent !important;
    &::-webkit-scrollbar {
      display: none;
    }
    li {
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 10px;
      padding: 10px 5px;
      font-size: 0.9rem;
      width: 15ch;
      cursor: pointer;
      height: fit-content;
      color: var(--inactive);
      transition: all 0.2s ease-in-out;
      flex-shrink: 0;
      &[data-active="true"] {
        // border-bottom: 2px inset var(--brand-fill);
        box-shadow: inset 0px -3px 0px 0px var(--brand-fill);
        color: black;
        transition: all 0.2s ease-in-out;
      }
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &-Body {
    display: flex;
    flex-flow: row nowrap;
    width: 100%;
    position: relative;
    overflow: hidden;
    &-Item {
      width: 100%;
      flex-shrink: 0;
      padding: 5px;
      height: 57vh;
      overflow: auto;
    }
  }
}

.Checkbox {
  &-Multiple {
    display: flex;
    flex-flow: row wrap;
  }
  &-Container {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    cursor: pointer;
    color: var(--text-light);
    &[data-rows="1"] {
      width: 100%;
      margin-bottom: 20px;
    }
    &[data-rows="2"] {
      width: 45%;
      &:nth-child(2n-1) {
        margin-right: 10%;
        margin-bottom: 20px;
      }
    }
    &:last-child {
      margin-right: 0;
      margin-bottom: 0;
    }
  }
  &-Indicator {
    width: var(--checkbox-indicator-dimension);
    height: var(--checkbox-indicator-dimension);
    // background: burlywood;
    margin-right: 15px;
    border-radius: calc(var(--checkbox-indicator-dimension) / 5);
    // border: 1px solid var(--base-background);
    box-shadow: inset 0px 0px 0px 1px var(--base-background);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.15s ease-in-out;
  }
  &-Container:hover {
    color: var(--text-dark);
    .Checkbox {
      &-Indicator {
        box-shadow: inset 0px 0px 0px 1px var(--text-light);
        transition: all 0.15s ease-in-out;
      }
    }
  }
  &-Container[data-active=true] {
    // background: burlywood;
    color: black;
    .Checkbox {
      &-Indicator {
        background-color: var(--brand-fill);
        box-shadow: none;
        transition: all 0.15s ease-in-out;
      }
    }
  }
  &-Limit {
    margin-top: 10px;
    color: var(--text-light);
  }
}

// Adjustment for dialog
.Dialog {
  &-Container {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    position: relative;
    &[data-side='right'] {
      text-align: right;
      align-items: flex-end;
      div {
        border-radius: 8px 0px 8px 8px;
        background: var(--chat-bubble-user);
        border: 1px solid var(--chat-bubble-user-border);
        color: var(--text-dark);
        &:before {
          right: -10px;
          border-color: var(--chat-bubble-user-tail) transparent transparent transparent;
          border-width: 10px 10px 0 0;
        }
      }
    }
    &[data-side='left'] {
      text-align: left;
      align-items: flex-start;
      div {
        border-radius: 0 8px 8px;
        background: var(--chat-bubble-bot);
        color: white;
        &:before {
          left: -10px;
          border-color: transparent var(--chat-bubble-bot-tail) transparent transparent;
          border-width: 0 10px 10px 0;
        }
      }
    }
  }
  &-Bubble {
    max-width: 80%;
    // width: 90%;
    padding: 10px 20px;
    border-radius: 0 8px 8px;
    line-height: 1.5rem;
    margin-bottom: 5px;
    &:before {
      content: '';
      display: flex;
      // width: 10px;
      // height: 10px;
      border-style: solid;
      position: absolute;
      // background: burlywood;
      top: 0px;
    }
    p {
      font-size: 1rem;
    }
  }
  &-Timestamp {
    font-size: 0.6rem;
    margin: 0 10px;
    color: var(--text-light);
  }
}

.Temporary {
  &-ChangeRender {
    position: absolute;
    bottom: 15px;
    right: 15px;
    padding: 10px 25px;
    background: var(--brand-fill);
    border: none;
    border-radius: 50px;
    font-size: 0.8rem;
    color: white;
    cursor: pointer;
    box-shadow: var(--box-shadow);
    z-index: 10000;
  }
  &-Sections {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: rgba(0, 0, 0, 0.3);
    z-index: 9999;
    // &[data-visible="true"] {
    //   display: flex;
    // }
    &[data-visible="false"] {
      display: none;
    }
    ul {
      list-style: none;
    }
    li {
      background: var(--brand-fill);
      padding: 10px 25px;
      margin-bottom: 10px;
      color: white;
      border-radius: 50px;
      cursor: pointer;
    }
  }
}



@media  (max-width: 900px) {
  :root {
    // Root measurement adjustment
    font-size: 14px;

    // Dimension adjustment
    --sidebar-width: 40px;
    --chatbox-width: 0; // Don't put unit, value in percentage
    --leftbox-width: 100; // Don't put unit, value in percentage
    --button-area-height: 65px;
    --icon-button-dimension: 40px;
    --dialog-input-height: 65px;
    --avatar-dimension: 90px;
    --layout-sidebar-adjustment: 50px;
    --mobile-menu-button-translation: 15px;

    // Theme color adjustment
    --brand-fill: #D53A4C;
    --brand-fill-test: #D53A4C66;
    --brand-gradient: linear-gradient(90.54deg, #F25767 0%, #D83445 100%);
    --light-background: #FFFFFF;
    --base-background: #E5E5E5;
    --text-dark: #707C97;
    --text-light: rgba(112, 124, 151, 0.7);
    --shadow: rgba(32, 32, 32, 0.08);
    --breakline: rgba(112, 124, 151, 0.15);
    --icon-light: white;
    --icon-dark: rgba(112, 124, 151, 0.5);
    --input-label: rgba(17, 17, 17, 0.48);

    // Theme standardisation
    --box-shadow: 0 4px 10px var(--shadow);
    --selected-box-shadow: 0 0 10px var(--brand-fill-test);
  }

  .AIVI {
    &-Sidebar {
      height: var(--sidebar-width);
      border-radius: 50%;
      overflow: hidden;
      position: absolute;
      top: var(--mobile-menu-button-translation);
      left: var(--mobile-menu-button-translation);
      z-index: 20;
    }
    &-Chatbox {
      display: none;
      &-Mobile {
        &-Show {
          opacity: 1;
          transition: opacity 0.3s ease-in-out;
          .Chatbox-Container {
            top: 0;
            transition: top 0.3s ease-in-out;
          }
        }
        &-Hide {
          opacity: 0;
          transition: opacity 0.3s ease-in-out;
          pointer-events: none;
          .Chatbox-Container {
            top: 100px;
            transition: top 0.3s ease-in-out;
          }
        }
        &-Model {
          display: flex;
          position: absolute;
          width: 100vw;
          height: 100vh;
          background: rgba(217, 217, 217, 0.6);
          backdrop-filter: blur(3px);
          z-index: 99;
          padding: 30px 20px;
          overflow: hidden;
        }
        &-Button {
          display: flex;
          align-items: center;
          justify-content: center;
          background: var(--brand-gradient);
          width: var(--mobile-chatbox-button);
          height: var(--mobile-chatbox-button);
          color: white;
          position: absolute;
          bottom: var(--mobile-chatbox-button-translation);
          left: var(--mobile-chatbox-button-translation);
          border-radius: 50%;
          z-index: 100;
          // border: 10px solid white;
        }
      }
    }
    &-Leftbox {
      padding: 75px 30px;
    }
  }

  .Leftbox {
    &-Submitted {
      img {
        width: 200px;
        height: 200px;
      }
    }
  }
}